.certificate {
  margin-top: 60px;
}

.certificate__title {
  @include small-header;

  margin-bottom: 30px;
}

.certificate__list {
  @include reset-list;
}

.certificate__list-item {
  margin-bottom: 20px;
}

.certificate__list-item-wrapper {
  display: grid;

  grid-gap: 30px;
  grid-template-columns: 1fr 1fr 1fr;
}
