.about-me {
  margin-top: 150px;
}

.about-me__title {
  @include purple-title;

  & span {
    @include simple-text;
  }
}

.about-me__subtitle {
  @include main-text;

  margin-top: 20px;
}

.about-me__list {
  @include reset-list;

  display: flex;
  margin-top: 30px;
}

.about-me__list-item {
  @include small-purple-text;

  margin-right: 76px;
  padding-top: 80px;
}

.about-me__list-item--quarantee {
  background-image: url("../img/icon-quarantee.webp");
  background-repeat: no-repeat;
  background-position: top center;
}

.about-me__list-item--certificate {
  background-image: url("../img/icon-certificate.webp");
  background-repeat: no-repeat;
  background-position: top center;
}

.about-me__list-item--education {
  background-image: url("../img/icon-education.webp");
  background-repeat: no-repeat;
  background-position: top center;
}

@media screen and (min-width: $tablet-width) and (max-width: 991px) {
  .about-me__title {
    margin-top: 30px;
  }

  .about-me__subtitle {
    font-size: 16px;
  }

  .about-me__list {
    justify-content: space-around;
  }

  .about-me__list-item {
    margin-right: 0;
  }
}

@media screen and (max-width: 1200px) {
  .about-me__list {
    justify-content: space-around;
  }

  .about-me__list-item {
    margin-right: 0;
  }
}

@media screen and (min-width: $mobile-width-only) and (max-width: $tablet-width) {
  .about-me__title {
    margin-top: 40px;
  }
}

@media screen and (max-width: $mobile-width-only) {
  .about-me {
    margin-top: 100px;
  }

  .about-me__title {
    margin-top: 30px;
  }

  .about-me__list {
    justify-content: space-around;
  }

  .about-me__list-item {
    margin-right: 0;
  }
}
