.page__body {
  background-image: url("../img/main-background.jpg");
  background-repeat: no-repeat;
  background-position: center top;
  background-size: 100% 600px;
}

.header {
  margin-top: 30px;

  background-color: $basic-white;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}

.page-header__mobile {
  @include purple-text;

  width: 100px;

  text-decoration: none;

  background-image: url("../img/icon-phone.png");
  background-repeat: no-repeat;
  background-position: left center;

  &:hover {
    color: $special-purple;

    background-image: url("../img/icon-phone-hover.png");
  }
}

.navbar-toggler-icon {
  background-image: url("../img/icons-menu.png");
  background-repeat: no-repeat;
}

.main-nav__list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
}

.navbar-collapse {
  display: flex;
  justify-content: space-between;
  width: auto;
}

.site-list__item {
  padding: 17px 20px;

  &:hover .site-list__link,
  &:hover {
    color: $basic-white;

    background-color: $special-yellow;
  }
}

.site-list__link {
  @include nav-text;

  text-decoration: none;

  &:hover {
    color: $basic-white;
  }
}

.site-list__item--active .site-list__link,
.site-list__item--active {
  color: $basic-white;

  background-color: $basic-yellow;
}

.page-header__country {
  @include purple-text;

  margin-right: 30px;
  margin-bottom: 0;
  margin-left: 50px;
  padding-left: 25px;

  background-image: url("../img/icon-world.webp");
  background-repeat: no-repeat;
  background-position: left center;
}

.navbar-expand-lg {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media screen and (min-width: $mobile-width-only) and (max-width: 991px) {
  .page-header__mobile {
    margin-left: 5px;

    font-size: 14px;
  }

  .page-header__country {
    width: 100%;
    margin-top: 20px;
    margin-right: 5px;

    font-size: 14px;
  }

  .site-list__item {
    padding: 17px 0;
    padding-right: 20px;
  }

  .site-list__item--active {
    color: $basic-yellow;

    background-color: transparent;

    & .site-list__link {
      color: $basic-yellow;

      background-color: transparent;
    }
  }

  .site-list__link {
    font-size: 12px;
  }

  .main-nav__list {
    width: 100%;
  }
}

@media screen  and (max-width: 1199px) {
  .main-nav__list {
    width: 100%;
  }
}

@media screen and (min-width: $tablet-width) and (max-width: $desktop-width) {
  .page__body {
    background-image: url("../img/main-background-desktop.jpg");
  }

  .header {
    padding: 10px;
  }

  .page-header__country {
    margin-bottom: 10px;
    margin-left: 0;
  }

  .navbar-collapse {
    flex-direction: column;
  }

  .navbar-expand-lg {
    flex-wrap: wrap;
  }

  .site-list__item--active {
    text-decoration: none;
  }
}

@media screen and (min-width: $mobile-width-only) and (max-width: $tablet-width) {
  .page__body {
    background-image: url("../img/main-background-tablet.jpg");
  }

  .header {
    padding-top: 10px;
  }
}


@media screen and (max-width: $tablet-width) {
  .header {
    padding: 10px;
  }

  .page-header__country {
    width: 100%;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: 70px;
  }

  .page-header__mobile {
    margin-left: 20px;
  }

  .navbar-collapse {
    flex-direction: column;
  }

  .navbar-expand-lg {
    flex-wrap: wrap;
  }

  .site-list__item--active {
    text-decoration: none;
  }
}

@media screen and (max-width: $mobile-width-only) {
  .page__body {
    background-image: url("../img/main-background-mobile.jpg");
    background-size: 100% 650px;
  }

  .header {
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 10px 0 10px 0;
  }

  .page-header__mobile {
    order: 1;
  }

  .page-header__country {
    order: 2;
  }

  .main-nav {
    order: 3;
  }

  .site-list__item {
    padding: 0;
    padding-top: 20px;
    padding-left: 25px;
  }

  .site-list__link {
    font-size: 14px;
  }

  .site-list__item--active {
    color: $basic-yellow;

    background-color: transparent;

    & .site-list__link {
      color: $basic-yellow;

      background-color: transparent;
    }
  }
}
