.portfolio {
  margin-top: 30px;
  padding: 70px 0;

  background-color: $basic-light-grey;
}

.portfolio__title {
  @include purple-title;

  text-align: center;
}

.portfolio__list {
  @include reset-list;

  margin-top: 50px;
}

.portfolio__list-item {
  display: flex;
  flex-direction: column;

  &:nth-child(even) .portfolio-info-wrapper {
    order: 1;
  }

  &:nth-child(even) .portfolio-img-wrapper {
    order: 2;
  }
}

.portfolio-item {
  display: flex;
  align-items: center;
  margin-bottom: 60px;
}

.portfolio__list-item-header {
  @include portfolio-header;

  & span {
    color: $basic-purple;
  }
}

.portfolio__list-item-description {
  @include main-text;

  margin-top: 15px;
  margin-bottom: 35px;
}

.portfolio__list-item-button {
  @include button;

  padding: 16px 61px;
}

@media screen and (max-width: $desktop-width) {
  .portfolio__list-item {
    margin-bottom: 40px;

    &:nth-child(even) .portfolio-info-wrapper {
      order: 1;
    }

    &:nth-child(even) .portfolio-img-wrapper {
      order: 2;
    }
  }

  .portfolio__list-item-header {
    order: 1;
    margin-top: 50px;
  }
}

@media screen and (min-width: $mobile-width-only) and (max-width: $tablet-width) {
  .portfolio__list-item {
    margin-bottom: 40px;

    &:nth-child(even) .portfolio-info-wrapper {
      order: 2;
    }

    &:nth-child(even) .portfolio-img-wrapper {
      order: 1;
    }
  }

  .portfolio__list-item-header {
    order: 1;
    margin-top: 50px;
  }
}

@media screen and (max-width: 991px) {
  .portfolio__list-item {
    margin-bottom: 40px;

    &:nth-child(even) .portfolio-info-wrapper {
      order: 2;
    }

    &:nth-child(even) .portfolio-img-wrapper {
      order: 1;
    }
  }

  .portfolio__list-item-header {
    order: 1;
    margin-top: 50px;
  }
}

@media screen and (max-width: $mobile-width-only) {
  .portfolio {
    padding: 70px 0 30px;
  }

  .portfolio__list-item-header {
    margin-top: 30px;
  }

  .portfolio__list-item {
    margin-bottom: 40px;

    &:nth-child(even) .portfolio-info-wrapper {
      order: 2;
    }

    &:nth-child(even) .portfolio-img-wrapper {
      order: 1;
    }
  }
}
