@mixin reset-list {
  margin: 0;
  padding: 0;

  list-style: none;
}

@mixin purple-text {
  font-weight: 700;
  font-size: 14px;
  color: $basic-purple;
}

@mixin nav-text {
  font-weight: 600;
  font-size: 12px;
  color: $basic-black;
  text-transform: uppercase;
}

@mixin main-screen-title {
  font-weight: 900;
  font-size: 50px;
  color: $basic-white;
}

@mixin main-screen-subtitle {
  font-weight: 400;
  font-size: 30px;
  color: $basic-white;
}

@mixin button {
  width: 250px;
  height: 50px;

  text-align: center;
  color: $basic-white;
  text-decoration: none;

  background-color: $basic-yellow;
  border: none;
  border-radius: 30px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);

  &:hover {
    color: $basic-white;

    background-color: $special-yellow;
  }
}

@mixin purple-title {
  font-weight: 900;
  font-size: 36px;
  color: $basic-purple;
  text-transform: uppercase;
}

@mixin simple-text {
  font-weight: 500;
  font-size: 18px;
  color: $basic-black;
  text-transform: none;
}

@mixin main-text {
  font-weight: 400;
  font-size: 16px;
  color: $basic-grey;
}

@mixin small-purple-text {
  font-weight: 500;
  font-size: 12px;
  text-align: center;
  color: $basic-purple;
  text-transform: uppercase;
}

@mixin small-header {
  font-weight: 500;
  font-size: 24px;
  text-transform: uppercase;
}

@mixin portfolio-header {
  font-weight: 600;
  font-size: 24px;
}
