.page-footer {
  background-color: $basic-purple;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 35px 0 25px;
}

.page-footer__mobile {
  @include purple-text;

  padding-top: 0;
  padding-left: 25px;

  color: $basic-white;
  text-decoration: none;

  background-image: url("../img/icon-phone-white.png");
  background-repeat: no-repeat;
  background-position: left center;

  &:hover {
    color: $special-purple;

    background-image: url("../img/icon-phone-hover.png");
  }
}

.page-footer__copyright {
  margin-bottom: 0;

  font-size: 14px;
  color: $basic-white;
}

.page-footer__country {
  @include purple-text;

  margin-right: 30px;
  margin-bottom: 0;
  padding-left: 25px;

  color: $basic-white;

  background-image: url("../img/icon-world-white.webp");
  background-repeat: no-repeat;
  background-position: left center;
}

@media screen and (max-width: $desktop-width) {
  .page-footer__copyright {
    margin-bottom: 0;

    font-size: 16px;
  }
}

@media screen and (max-width: $mobile-width-only) {
  .footer {
    flex-direction: column;
  }

  .page-footer__mobile {
    margin-bottom: 20px;

    font-size: 16px;
  }

  .page-footer__copyright {
    margin-bottom: 20px;

    font-size: 16px;
  }

  .page-footer__country {
    font-size: 16px;
  }
}
