.main-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 350px;
  margin-top: 60px;
}

.main-screen__title {
  @include main-screen-title;

  text-align: center;
}

.main-screen__subtitle {
  @include main-screen-subtitle;

  margin-top: 10px;

  font-size: 25px;
  text-align: center;
}

.main-screen__list {
  @include reset-list;

  margin-top: 20px;
}

.main-screen__list-item {
  margin-bottom: 10px;
  padding-left: 30px;

  font-weight: 400;
  font-size: 18px;
  color: $basic-white;

  background-image: url("../img/icon-check.png");
  background-repeat: no-repeat;
  background-position: left center;
}

.main-screen__button {
  @include button;

  margin-top: 20px;
  padding: 14px 10px;
}

@media screen and (min-width: $desktop-width) {
  .main-screen__title {
    text-align: center;
  }
}

@media screen and (min-width: $tablet-width) and (max-width: $desktop-width) {
  .main-screen__title {
    text-align: center;
  }
}

@media screen and (max-width: 400px) {
  .main-screen__title {
    font-size: 40px;
  }

  .main-screen__list-item {
    font-size: 14px;
  }
}

@media screen and (max-width: $mobile-width-only) {
  .main-screen__title {
    text-align: center;
  }

  .main-screen {
    min-height: 450px;
  }
}
