.skills {
  margin-top: 50px;
}

.skills__title {
  @include small-header;

  margin-bottom: 30px;
}

.skills__list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
  margin-top: 30px;
}

.skills__list-item {
  @include small-header;

  margin-bottom: 40px;

  font-size: 18px;
  text-transform: none;

  & p {
    height: 36px;
    margin: 0 0 8px;
    padding-left: 46px;

    background-repeat: no-repeat;
    background-position: left center;
    background-size: 40px;
  }
}

.level {
  height: 24px;
  margin: 0 0 40px;
  overflow: hidden;

  font-weight: 500;
  font-size: 14px;
  text-align: right;
  color: $basic-white;

  background-color: $special-grey;
  border-radius: 12px;
  box-shadow: inset 0 3px 3px 0 rgba(0, 0, 0, 0.2);

  & div {
    height: 24px;
    margin: 0;
    padding-right: 10px;

    line-height: 26px;
    letter-spacing: 1px;

    background-color: $basic-purple;
    border-radius: 12px;
  }
}

.skill-html {
  background-image: url("../img/icon-html.png");
}

.skill-css {
  background-image: url("../img/icon-css.png");
}

.skill-photoshop {
  background-image: url("../img/icon-adobe-photoshop.png");
}

.skill-sass {
  background-image: url("../img/icon-sass.png");
}

.skill-github {
  background-image: url("../img/icons-github.png");
}

.skill-gulp {
  background-image: url("../img/icon-gulp.png");
}
